* {
  box-sizing: border-box;
}

body, html {
  height: 100%;
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
}

#root {
  height: auto;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.editorBox {
    grid-area: editor;
    justify-self: center;
    width: 500px;
    height: 200px;
    margin-top: 20px;
}

.editorHeader {
    display: flex;
    justify-content: space-between;
    padding: 0px 5px 0 5px;
    align-items: center;
    width: inherit;
    background: #4FB3C1;
    border: 1px solid black;
}

.editorBox textarea {
    width: inherit;
    height: 200px;
    background: rgb(169, 218, 224);
    border: 1px solid black;
    overflow-y: scroll;
}
.previewBox {
    grid-area: preview;
    justify-self: center;
    width: 60%;
    height: 100%;
    margin-top: 60px;
    height: auto;
}


.previewBox h1 {
    margin: 0;
}

.previewHeader {
    width: 100%;
    background: #4FB3C1;
    border-bottom: 2px solid black;
    padding-left: 15px;
    padding-right: 15px;
}

.previewContent {
    width: 100%;
    height: 100%;
    background: rgb(169, 218, 224);
    padding: 15px;
}

.previewContent h1 {
    border-bottom: solid black;
}

.previewContent h2 {
    border-bottom: 1px solid black;
}

.previewContent code, pre {
    background-color: white;
}

.previewContent blockquote p {
    border-left: 3px solid rgb(26, 50, 53);
    padding-left: 5px;
    color: rgb(26, 50, 53);
    font-weight: 450;
}

.previewContent table {
    border: 1px solid #1C6EA4;
    background-color: #EEEEEE;
    width: 100%;
    text-align: left;
    border-collapse: collapse;
}

.previewContent td, th {
  border: 1px solid #AAAAAA;
  padding: 3px 2px;
}
tbody, td {
  font-size: 13px;
}
.App {
    display: grid;
    width: 100%;
    height: 100%;
    background: rgb(39, 104, 129);
    grid-template-columns: 1fr;
    grid-template-rows: 0.2fr 1.8fr;
    grid-template-areas:
    "editor"
    "preview";
}

